
/**Styling scrollable elements*/

.js-scroll {
  opacity: 0;
  transition: opacity 500ms;
}

.js-scroll.scrolled {
  opacity: 1;
}

.scrolled.fade-in-bottom {
  animation: fade-in-bottom 1s ease-in-out both;
}
.scrolled.fade-in-bottom.two-sec {
  animation: fade-in-bottom 2s ease-in-out both;
}
.scrolled.fade-in-bottom.three-sec {
  animation: fade-in-bottom 3s ease-in-out both;
}
.scrolled.fade-in-bottom.four-sec {
  animation: fade-in-bottom 4s ease-in-out both;
}


